export const query = graphql`
  query {
    craft {
      seomatic(uri: "/about") {
        metaTitleContainer
        metaTagContainer
        metaLinkContainer
        metaScriptContainer
        metaJsonLdContainer
      }

      entries(section: [projects]) {
        ... on Craft_Projects {
          disciplines {
            title
          }
        }
      }
    }
  }
`;
